<template>
    <!-- 新增货源信息 -->
    <div class="supply">
        <navigate title="货源信息" position="absolute">
            <mu-button slot="right" color="#fff" @click="handlerSubmit" flat>发送</mu-button>
        </navigate>
        <div class="scroll-wrapper">
            <div class="picture-wrapper border-1px-b">
                <div class="picture" v-if="item" v-for="(item, index) in images" :key="index"
                     @click="handlerBrowse(index)"
                     :style="`background-image: url(${item})`">
                    <mu-button color="#f44336" icon small @click.stop="onDeletePicture(item, index)">
                        <mu-icon value="delete"/>
                    </mu-button>
                </div>
                <mu-ripple class="picture mu-picker-display" @click="handlerPicture" v-if="images < 4">
                    <mu-icon class="append" color="#fafafa" value="add_a_photo" size="50"/>
                </mu-ripple>
            </div>
            <mu-form class="form" :model="form" label-position="left" label-width="100" ref="form">
                <mu-form-item class="form-item-s border-1px-b" prop="name" label="货物名称" :rules="nameRules">
                    <mu-text-field v-model="form.name" placeholder="请输入货物名称" prop="name" solo/>
                </mu-form-item>
                <mu-form-item class="form-item-s border-1px-b" prop="type" label="货物类型" :rules="goodsRules">
                    <mu-select v-model="form.type" placeholder="请输入货物类型" solo>
                        <mu-option v-for="(option, index) in goodsTypeOptions" :key="option.name" :label="option.name"
                                   :value="option.name"/>
                    </mu-select>
                </mu-form-item>
                <mu-form-item class="form-item-s border-1px-b" prop="weight" label="货物重量" :rules="weightRules">
                    <mu-text-field v-model="form.weight" placeholder="请输入货物重量"
                                   suffix="吨" prop="weight" type="number"
                                   solo/>
                </mu-form-item>
                <mu-form-item class="form-item-s border-1px-b" prop="price" label="最高限价" :rules="priceRules">
                    <mu-text-field v-model="form.price" placeholder="请输入最高限价"
                                   suffix="元/吨" prop="price" type="number"
                                   solo/>
                </mu-form-item>
                <mu-form-item class="form-item-s border-1px-b" label="装货时间" prop="time"
                              :rules="timeRules">
                    <mu-date-input v-model="form.time"
                                   type="dateTime"
                                   view-type="list"
                                   container="bottomSheet"
                                   placeholder="请选择装货时间"
                                   solo/>
                </mu-form-item>
                <mu-form-item class="form-item-s border-1px-b" prop="loadingPort" label="装货港" :rules="portRules">
                    <mu-select v-model="form.loadingPort" placeholder="请输入装货港" solo>
                        <mu-option v-for="(option, index) in portTypeOptions" :key="option.name" :label="option.name"
                                   :value="option.name"/>
                    </mu-select>
                </mu-form-item>
                <mu-form-item class="form-item-s border-1px-b" prop="unloadingPort" label="卸货港" :rules="portRules">
                    <mu-select v-model="form.unloadingPort" placeholder="请输入卸货港" solo>
                        <mu-option v-for="(option, index) in portTypeOptions" :key="option.name" :label="option.name"
                                   :value="option.name"/>
                    </mu-select>
                </mu-form-item>
                <mu-form-item class="form-item-s border-1px-b" prop="phone" label="联系方式" :rules="phoneRules">
                    <mu-text-field v-model="form.phone" placeholder="请输入联系方式" solo/>
                </mu-form-item>

            </mu-form>
        </div>
    </div>
    <!-- 新增货源信息 -->
</template>

<script>
    import Navigate from "../../base/navigate/navigate"
    import {saveMoment} from "../../api/dynamic"
    import {queryTypeDictByFid} from "../../api/search"
    import {isArray} from "../../types/index"
    import {formatDate} from '../../common/utils/time'
    import ImagePreview from "vant/lib/image-preview"
    import {uploadBase64} from "../../api/sys"
    import {getPictures, getBase64} from "../../common/js/cordova/camera"

    export default {
        name: "supply",
        data() {
            return {
                visible: false,
                addVisible: true,
                form: {
                    name: "",
                    type: "",
                    weight: "",
                    price: "",
                    time: "",
                    loadingPort: "",
                    unloadingPort: "",
                    phone: ""
                },
                images: [],
                defaultList: [],
                submitFlag: false,
                /***
                 * 表单验证
                 */
                nameRules: [
                    {validate: val => !!val, message: '必须填写货物名称'},
                    {validate: val => val.length >= 2, message: '货物名称长度大于或等于 2 '}
                ],
                weightRules: [
                    {validate: val => !!val, message: '必须填写货物重量'},
                    {validate: val => /^(-?\d+)(\.\d{1,2})?$/.test(val), message: '请精确到小数点后两位'}
                ],
                priceRules: [
                    {validate: val => /^(-?\d+)(\.\d{1,2})?$/.test(val), message: '请精确到小数点后两位'}
                ],
                timeRules: [
                    {validate: val => !!val, message: '必须选择装货时间'},
                ],
                goodsRules: [
                    {validate: val => !!val, message: '必须选择货物类型'},
                    {
                        validate: val => this.goodsTypeOptions.findIndex(item => item.name === val) !== -1,
                        message: '请选择正确的货物类型'
                    }
                ],
                portRules: [
                    {validate: val => !!val, message: '必须选择港口'},
                    {
                        validate: val => this.portTypeOptions.findIndex(item => item.name === val) !== -1,
                        message: '请选择正确的港口类型'
                    }
                ],
                phoneRules: [
                    {validate: val => !!val, message: '必须填写手机号码'},
                    {validate: val => /^1\d{10}$/.test(val), message: '请输入正确的手机号码'}
                ],
                /***
                 * 下拉选项：
                 *      value:  值
                 *      name:  名称
                 */
                goodsTypeOptions: [],
                portTypeOptions: []
            }
        },
        created() {
            this.queryTypeDictByFid()
        },
        activated() {
            this.form = {
                name: "",
                type: "",
                weight: "",
                price: "",
                time: "",
                loadingPort: "",
                unloadingPort: "",
                phone: ""
            }

            this.images = []
            this.defaultList = []
        },
        methods: {
            async queryTypeDictByFid() {
                this.goodsTypeOptions = isArray(await queryTypeDictByFid('009'))

                this.portTypeOptions = isArray(await queryTypeDictByFid('019'))
            },
            handlerSubmit() {
                this.$refs.form.validate().then(result => {
                    if (result) {
                        this.submit()
                    }
                })
            },
            async submit() {
                const loading = this.$loading();

                const data = {
                    content: this.splicingContent(),
                    type: '004002',
                    mediaType: '003002',
                    status: '020002'
                }

                if (this.images.length) {
                    const images = await Promise.all(this.images.map(item => {
                        return uploadBase64(item)
                    }))

                    data.mediaPath = isArray(images).join(';')
                }

                await saveMoment(data)

                loading.close()

                this.$toast('发表成功！')

                this.$emit('refresh')

                this.$router.go(-2)
            },
            /***
             * 拼接提交内容
             */
            splicingContent() {
                return `<p>货物名称:${this.form.name}</p><p>货物类型:${this.form.type}</p><p>货物重量:${this.form.weight}吨</p><p>最高限价:${this.form.price}</p><p>装货时间:${formatDate(this.form.time, 'YYYY-MM-DD HH:mm:ss')}</p><p>装货港:${this.form.loadingPort}</p><p>卸货港:${this.form.unloadingPort}</p><p>联系方式:${this.form.phone}</p>`
            },
            /***
             * 点击选择图片
             */
            handlerPicture() {
                getPictures(4, this.defaultList).then(images => {
                    this.images = []
                    this.defaultList = []

                    Array.prototype.map.call(images, item => {
                        this.defaultList.push(item.path)

                        getBase64(item).then(res => {
                            this.images.push(`data:image/jpeg;base64,${res.thumbnailBase64}`)
                        })
                    })
                })
            },
            /***
             * 点击浏览图片
             */
            handlerBrowse(index) {
                ImagePreview({
                    images: this.images,
                    startPosition: index
                })
            },
            /***
             * 点击删除图片
             */
            onDeletePicture(item, index) {
                this.images.splice(index, 1)

                this.defaultList.splice(index, 1)
            }
        },
        components: {
            Navigate
        }
    }
</script>

<style lang="stylus" scoped>
    .supply
        padding-top 46px
        background-color #fff

        .appbar
            position absolute
            top 0
            left 0
            z-index 1
            width 100%
            height 46px
            box-shadow none
            background-color #3899D7

            .title
                color #fff
                line-height 46px
                text-align center
                font-size 15px

        .scroll-wrapper
            width 100%
            height 100%

            .form
                .form-item-s
                    margin 0
                    padding 10px

                    .mu-form-item-help
                        bottom 5px

                .form-item-buttons
                    padding 20px 0

                    .button
                        color #fff
                        background-color #2196f3

                        &.clear
                            color #999
                            background-color #fff

                .button-wrapper
                    padding 20px 0

            .picture-wrapper
                display flex
                flex-wrap wrap
                padding 5px
                width 100%

                .picture
                    position relative
                    margin-right 3px
                    margin-bottom 3px
                    padding-top calc(25% - 3px)
                    height 0
                    width calc(25% - 3px)
                    background-position center center
                    background-repeat no-repeat
                    background-size cover

                    .mu-button
                        position absolute
                        top 0
                        right 0

                .append
                    position absolute
                    top 50%
                    left 50%
                    transform translate3d(-50%, -50%, 0)

</style>



